.section {
    // background-color: '#F5F5F5';
    height: 100%;
    width: 100%;

    scroll-snap-align: start;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // font-size: 3rem;
    background-color: radial-gradient(
      circle,
      rgba(245, 245, 245, 1) 0%,
      rgba(203, 203, 203, 1) 100%
    );

}

.frame {
  background: radial-gradient(
  circle,
  rgba(245, 245, 245, 1) 0%,
  rgba(245, 245, 245, 1) 75%,
  rgba(203, 203, 203, 1) 100%
);

    height: 100%;
    width: 100%;
    position: fixed;

    overflow-y: scroll;
    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
  
    /* optional: hide scroll bar */
    scrollbar-width: none; /* Firefox */
    &::-webkit-scrollbar {
      display: none; /* Chrome / Safari */
    }
}

.top-button{
  position: fixed;
  width: 5vh;
  height: 5vh;
  border: solid;
  border-radius:10vh;
  bottom: 3vh;
  right: 3vw;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  &-white {
    color: white;
    position: fixed;
    width: 5vh;
    height: 5vh;
    border: solid;
    border-radius:10vh;
    bottom: 3vh;
    right: 3vw;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
}